import moveVerify from '@/components/helang-moveVerify/helang-moveVerify.vue';
import md5 from '../../static/js/md5.js';
import rs from '../../static/js/request.js';
const app = getApp().globalData;
const { navBar, appid, appsecret } = app;
export default {
	components: {
		moveVerify
	},
	data() {
		return {
			address: '',
			navBar: navBar,
			nickname: '',
			mobile: '',
			password: '',
			confirm_pwd: '',
			number_code: '',
			verify_code: '',
			showcode: false
		};
	},
	methods: {
		leftClick() {
			uni.navigateBack({
				delta: 1
			});
		},
		// 滑动验证
		verifyResult() {
			let timeStamp = Math.round(new Date().getTime() / 1000);
			let obj = {
				appid: appid,
				timeStamp: timeStamp
			};
			let sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
			let params = Object.assign(
				{
					sign: sign
				},
				obj
			);
			rs.getRequests('random', params, res => {
				let data = res.data;
				if (data.code == 200) {
					this.number_code = data.data.number;
					console.log(this.number_code);
					uni.setStorageSync('laravel_session', res.header['Set-Cookie']);
				} else {
					uni.showToast({
						title: data.msg,
						duration: 2000,
						icon: 'none'
					});
				}
			});
		},
		getCode() {
			let timeStamp = Math.round(new Date().getTime() / 1000);
			if (!this.mobile) {
				uni.showToast({
					title: '手机号不能为空',
					duration: 2000,
					icon: 'none'
				});
				return;
			}
			if (!this.number_code) {
				uni.showToast({
					title: '请先拖动滑块',
					duration: 2000,
					icon: 'none'
				});
				return;
			}

			let obj = {
				appid: appid,
				mobile: this.mobile,
			
				timeStamp: timeStamp
			};
			var that = this;
			let sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
			  var random_str = md5.hexMD5(appsecret + this.number_code);
			let params = Object.assign(
				{
					sign: sign ,secret_str: random_str, active: 2
				},
				obj
			);
			rs.postRequest('sendCode',params,(res)=>{
				let {data}=res;
			if(data.code==200){
				this.$refs.code.sendCode();
			}else{
				uni.showToast({
					title: data.msg,
					duration: 2000,
					icon: 'none'
				});
			}
			});
			
		}
	}
};
